#loading {
  min-width: 100%;
}

.navbar {
  border-radius: 0px !important;
}

.bg-dark {
  background-image: none;
  background-color: #ff2e2e;
}

.navbar-nav > li > a {
  color: #fefefe !important;
}

.navbar-nav > li > a:hover {
  border-color: #dd1f1f;
}

.navbar-nav > .active > a {
  color: #fefefe !important;
  background-image: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgb(250, 30, 30)),
    to(rgb(250, 40, 40))
  ) !important;
  background-image: -webkit-linear-gradient(
    top,
    rgb(250, 30, 30) 0%,
    rgb(250, 40, 40) 100%
  ) !important;
  background-image: -o-linear-gradient(
    top,
    rgb(250, 30, 30) 0%,
    rgb(250, 40, 40) 100%
  ) !important;
  background-image: linear-gradient(
    to bottom,
    rgb(250, 30, 30) 0%,
    rgb(250, 40, 40) 100%
  ) !important;
}

.navbar-brand {
  color: #fefefe !important;
}

.menu-row {
  padding-top: 0.4em;
  padding-bottom: 0.4em;
}

#modal-change-guest .row {
  padding-top: 0.4em;
  padding-bottom: 0.4em;
}

#modal-change-table .row {
  padding-top: 0.4em;
  padding-bottom: 0.4em;
}



